<template>
	<view>
		<!-- 充值 H5页面 -->
		<web-view 
			:webview-styles="webviewStyles" 
			:src="url"
			>
		</web-view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// H5充值页面的样式
				webviewStyles: {
					progress: {
						color: '#FF3333'
					}
				},
				url:'',//跳转到H5页面的地址
			};
		},
		onLoad(options) {
			this.url = `http://localhost:8080?token=${wx.getStorageSync('token')}&txtId=${options.txtId}`
		}
	}
</script>

